import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import axios from 'axios';

// Individual component imports //
import CopyRightFooter from './components/CopyrightFooter/CopyrightFooter';

// Page imports //
import AboutPage from "./pages/AboutPage";
import ContactPage from "./pages/ContactPage";
import HelpPage from "./pages/HelpPage";
import Home from "./pages/Home";
import SigninPage from "./pages/SigninPage";
import SignupPage from "./pages/SignupPage";
import UserDash from "./pages/UserDash";


class App extends Component {
  constructor() {
    super()
    this.state = { 
      loggedIn: false,
      username: null
    }

    this.getUser = this.getUser.bind(this)
    this.componentDidMount = this.componentDidMount.bind(this)
    this.updateUser = this.updateUser.bind(this)
  }

  componentDidMount() {
    this.getUser()
  };

  updateUser (userObject) {
    this.setState(userObject)
    this.props.history.push("/dash")
  };

  getUser() {
    axios.get('/api/user').then(response => {
      console.log('Get user response: ')
      console.log(response.data)
      if (response.data.user) {
        console.log('Get user: There is a user saved in the server session: ')
        this.setState({
          loggedIn: true,
          username: response.data.user.username
        })
      } else {
        console.log('Get user: no user saved in current session');
        this.setState({
          loggedIn: false,
          username: null
        })
      }
    })
  };
  
  render() {
    return (
      <Router>
        <div className="App">
          <Switch>
            <Redirect from="/" exact path to="/home" />
            <Route exact path="/home" component={Home} />
            <Route exact path="/about" component={AboutPage} />
            <Route exact path="/contact" component={ContactPage} />
            <Route exact path="/help" component={HelpPage} />
            <Route exact path="/signin" render={() => <SigninPage updateUser={this.updateUser} /> } />
            <Route exact path="/signup" component={SignupPage} />
            <Route exact path="/dash" component={UserDash} />
            <Route exact path="/signout" component={Home} />
          </Switch>
          <CopyRightFooter />
        </div>
      </Router>
    );
  }
}

export default App;



